import React from 'react'
import CardContainer from '../containers/CardContainer'

export default () => {
    return (
        <div className="row">
            <div className="col-md-12 col-sm-12">
                <CardContainer bgHeader='bg-primary' textHeader='Search' cardActions={['toggler']} number='1'>
                    <div className="row">
                        <div className="col-md-12">
                            <form className="form ">
                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4  control-label" for="form-control-3">OR No.</label>
                                        <div className="col-sm-6 col-md-6 ">
                                            <input tabindex="2" type="text" id="text" className="form-control"></input>
                                        </div>
                                    </div>


                                </div>
                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4 control-label" for="form-control-3">Plan Due Date</label>
                                        <div className="col-sm-6 col-md-6">
                                            <input id="job_due_date" className="form-control" type="text" placeholder="" />
                                        </div>
                                    </div>

                                </div>

                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4 control-label" for="form-control-3">Brand</label>
                                        <div className="col-sm-6 col-md-6">
                                            <select tabindex="3" id="payment-method" className="form-control" multiple="multiple">
                                                <option value="" selected="selected">All</option>
                                                <option value="administrator" >Crystal</option>
                                                <option value="administrator" >XXX</option>
                                                <option value="administrator" >XXX</option>
                                            </select>
                                        </div>
                                    </div>

                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-3 control-label" for="form-control-3">Sales Group</label>
                                        <div className="col-sm-6 col-md-6">
                                            <select tabindex="5" className="form-control" name="sales-group" id="sales-group" data-dropdown-align-right="true" data-style="btn-default btn-sm" data-width="fit">
                                                <option value="" selected="selected">All</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4 control-label" for="form-control-3">Department</label>
                                        <div className="col-sm-6 col-md-6">
                                            <select tabindex="5" className="form-control" name="department" id="department" data-dropdown-align-right="true" data-style="btn-default btn-sm" data-width="fit">
                                                <option value="" selected="selected">All</option>
                                            </select>
                                        </div>
                                    </div>

                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-3 control-label" for="form-control-3">Section</label>
                                        <div className="col-sm-6 col-md-6">
                                            <select tabindex="5" className="form-control" name="section" id="section" data-dropdown-align-right="true" data-style="btn-default btn-sm" data-width="fit">
                                                <option value="" selected="selected">All</option>
                                            </select>
                                        </div>
                                    </div>

                                </div>

                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4  control-label" for="form-control-3">Created By</label>
                                        <div className="col-sm-6 col-md-6 ">
                                            <input tabindex="2" type="text" id="text" className="form-control"></input>
                                        </div>
                                    </div>

                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-3  control-label" for="form-control-3">Customer Name</label>
                                        <div className="col-sm-6 col-md-6 ">
                                            <input tabindex="2" type="text" id="text" className="form-control"></input>
                                        </div>
                                    </div>


                                </div>

                                <div className="row">
                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-4  control-label" for="form-control-3">TOS No.</label>
                                        <div className="col-sm-6 col-md-6 ">
                                            <input tabindex="2" type="text" id="text" className="form-control"></input>
                                        </div>
                                    </div>

                                    <div className="form-group col-md-6">
                                        <label className="col-sm-6 col-md-3  control-label" for="form-control-3">Status</label>
                                        <div className="col-sm-6 col-md-6">
                                            <select tabindex="1" id="demo-select2-11" className="form-control" multiple="multiple">
                                                <option value="" selected="selected">All</option>
                                                <option value="SM" >Wait For SM Approve</option>
                                                <option value="TOS" >Wait For Create TOS</option>
                                            </select>
                                        </div>
                                    </div>


                                </div>

                                <div className="row">
                                    <div className="form-group col-md-4">
                                        <button className="btn btn-success btn-sm btn-labeled" type="button">
                                            <span className="btn-label">
                                                <span className="icon icon-search icon-lg icon-fw"></span>
                                            </span>
                                            SEARCH
                                            </button> <a className="btn btn-danger btn-sm btn-labeled" href="order-requirement-detail.html">
                                            <span className="btn-label">
                                                <span className="icon icon-plus icon-lg icon-fw"></span>
                                            </span>
                                            Create
                                            </a>

                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </CardContainer>


                <CardContainer bgHeader='bg-primary' textHeader='Result' cardActions={['toggler']} number='2'>
                    <div className="table-responsive">
                        <table id="demo-datatables-buttons-2" className="table table-bordered  table-nowrap dataTable" cellspacing="0" width="100%">
                            <thead>
                                <tr>
                                    <th>OR No.</th>
                                    <th>Sales<br />Group</th>
                                    <th>Department</th>
                                    <th>Section</th>
                                    <th>Customer<br />Name</th>
                                    <th>Plan Due<br />Date</th>
                                    <th>TOS No.</th>
                                    <th>Created<br /> By</th>
                                    <th>Status</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td><a href="order-requirement-detail.html?or_no=xxxxx">1/2018</a></td>
                                    <td>784</td>
                                    <td>Planning</td>
                                    <td>sales and operation planning</td>
                                    <td>  </td>
                                    <td>30/03/2018</td>
                                    <td> </td>
                                    <td>John Wick</td>
                                    <td><span className="label label-info">Waiting For SM Approve</span></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </CardContainer>


            </div>
        </div>
    )
}


