import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'

import OrderCreate from './components/OrderCreate'
import OrderSearch from './components/OrderSearch'
import OrderSaleList from './components/OrderSaleList'
import OrderRequirement from './components/OrderRequirement'
import OrderRequirementDetail from './components/OrderRequirementDetail'
import MasterPrice from './components/MasterPrice'
import MasterSafety from './components/MasterSafety'
import Tracking from './components/Tracking'
import ManageShipment from './components/ManageShipment'
import Home from './components/Home'
import MainLayout from './components/Layout'


const AppRoute = ({ component: Component, layout: Layout, ...rest }) => (
    <Route {...rest} render={props => (
        <Layout>
            <Component {...props} />
        </Layout>
    )} />
)

export default () => {
    return (
        <BrowserRouter>
            <Switch>
                <AppRoute path="/" exact layout={MainLayout} component={Home} />
                <AppRoute path="/order" exact layout={MainLayout} component={OrderSearch} />
                <AppRoute path="/order/create" exact layout={MainLayout} component={OrderCreate} />
                <AppRoute path="/order/listsaleorder" exact layout={MainLayout} component={OrderSaleList} />
                <AppRoute path="/order/orderrequirement" exact layout={MainLayout} component={OrderRequirement} />
                <AppRoute path="/order/orderrequirementdetail" exact layout={MainLayout} component={OrderRequirementDetail} />
                <AppRoute path="/master/price" exact layout={MainLayout} component={MasterPrice} />
                <AppRoute path="/master/safety" exact layout={MainLayout} component={MasterSafety} />
                <AppRoute path="/shipment/mangement" exact layout={MainLayout} component={ManageShipment} />
                <AppRoute path="/tracking" exact layout={MainLayout} component={Tracking} />
                {/* <Route path="/login" component={Login} /> */}
            </Switch>

        </BrowserRouter>

    )
}


