import React from 'react'
import SideBarContainer from '../containers/SideBarContainer'

export default (props) => {
    return (
        <div className="layout layout-header-fixed layout-sidebar-fixed">
            <div className="layout-header">
                <div className="navbar navbar-default">
                    <div className="navbar-header">
                        <a className="navbar-brand navbar-brand-center">
                            SCG Packaging
                            </a>
                        <button className="navbar-toggler visible-xs-block collapsed" type="button" data-toggle="collapse" data-target="#sidenav">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="bars">
                                <span className="bar-line bar-line-1 out"></span>
                                <span className="bar-line bar-line-2 out"></span>
                                <span className="bar-line bar-line-3 out"></span>
                            </span>
                            <span className="bars bars-x">
                                <span className="bar-line bar-line-4"></span>
                                <span className="bar-line bar-line-5"></span>
                            </span>
                        </button>
                        <button className="navbar-toggler visible-xs-block collapsed" type="button" data-toggle="collapse" data-target="#navbar">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="arrow-up"></span>
                            <span className="ellipsis ellipsis-vertical">
                                <img className="ellipsis-object" width="32" height="32" src="img/0180441436.jpg" alt="Teddy Wilson" />
                            </span>
                        </button>
                    </div>
                    <div className="navbar-toggleable">
                        <nav id="navbar" className="navbar-collapse collapse">
                            <button className="sidenav-toggler hidden-xs" title="Collapse sidenav ( [ )" aria-expanded="true" type="button">
                                <span className="sr-only">Toggle navigation</span>
                                <span className="bars">
                                    <span className="bar-line bar-line-1 out"></span>
                                    <span className="bar-line bar-line-2 out"></span>
                                    <span className="bar-line bar-line-3 out"></span>
                                    <span className="bar-line bar-line-4 in"></span>
                                    <span className="bar-line bar-line-5 in"></span>
                                    <span className="bar-line bar-line-6 in"></span>
                                </span>
                            </button>
                        </nav>
                    </div>
                </div>
            </div>
            <div className="layout-main">
                <div className="layout-sidebar">
                    <div className="layout-sidebar-backdrop"></div>
                    <SideBarContainer />
                </div>
                <div className="layout-content">
                    <div className="layout-content-body">
                        {props.children}
                    </div>
                </div>
            </div>
        </div>
    )

}


