import React, { Component } from 'react'
import SideBar from '../components/SideBar'

class NavItem {
    constructor({ headText, subItem, path, txt, icon, updateState, clearActiveState,key }) {
        this.headText = headText
        this.subItem = subItem
        this.path = path
        this.txt = txt
        this.icon = icon
        this.clearActiveState = clearActiveState
        this.key=key
        this.updateState = updateState
        if(this.subItem){
            this.subItem.forEach((item)=>{
                item.activeHeadState = ()=>{
                   this.clearActiveState()
                   this.active=true
                }
            })
        }
            
    }

    onClick = () => {
        if(this.activeHeadState){
            this.activeHeadState()
        }
        else if(!this.subItem){
            this.clearActiveState()
        }else{
            //is head click
            this.updateState()
        }
        this.active = true;
    }
}

export default class SideBarContainer extends Component {

    constructor(props) {
        super(props)
        this.state = {
            menuItems: [
                new NavItem({ key:11,path: "/", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Home", icon: "icon-home" }),
                new NavItem({ key:1,path: "/master/price", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Master Price", icon: "icon-money" }),
                new NavItem({ key:2,path: "/master/safety", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Master Safety", icon: "icon-download" }),
                new NavItem({ key:3,path: "/shipment/mangement", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "ManageShipment", icon: "icon-ship" }),
                new NavItem({ key:4,path: "/tracking", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Tracking", icon: "icon-truck" }),
                new NavItem({
                    key:5,headText: "Order", updateState: this.updateState, clearActiveState: this.clearActiveState, subItem: [
                        new NavItem({ key:6,path: "/order", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Search Order" }),
                        new NavItem({ key:7,path: "/order/create", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Create Order" }),
                        new NavItem({ key:8,path: "/order/listsaleorder", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "List Sale Order" }),
                        new NavItem({ key:9,path: "/order/orderrequirement", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Order Requirement" }),
                        new NavItem({ key:10,path: "/order/orderrequirementdetail", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Order Requirement Detail" }),
                    ]
                })
            ]
        }
    }

    updateState = () => {
        this.setState(this.state)
    }

    clearActiveState = () => {
        this.state.menuItems.forEach((item) => {
            item.active = false
            if(item.subItem)
                item.subItem.forEach((itemSub)=>{
                    itemSub.active = false
                });
        })
        this.updateState()
    }

    render() {

        let prop = {
            menuItems: this.state.menuItems
        }
        return (
            <SideBar {...prop} />

        )
    }
}


